.slash {
	color: $color-red;
	font-weight: 500;
}

.title {
	font-size: 17px;
	font-weight: 450;
	text-transform: uppercase;

	@include md-min {
		font-size: 22px;
	}

	.section-story & {
		margin-top: 0;
	}

	.partners & {
		@include content-width();
		margin: 0 auto;
	}

	@include md-max {
		margin: 30px 0;
	}
}
