.big-image {
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-size: cover;
	height: 60vh;
	background-image: url(../assets/images/header/header.jpg);
	width: 100vw;
	position: relative;
	top: 0;
	background-position: center;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: black;
		opacity: 0.4;
	}
}
